<template>
  <div class="main">
    <landing></landing>
    <photos></photos>
    <splited-presets></splited-presets>
    <about-me></about-me>
  </div>
  </div>
</template>

<script>
  import Landing from './Landing.vue'
  import Photos from './Photos.vue'
  import SplitedPresets from './SplitedPresets.vue'
  import AboutMe from './AboutMe.vue'

  export default {
    data() {
      return {}
    },
    components: {
      'landing': Landing,
      'photos': Photos,
      'splited-presets': SplitedPresets,
      'about-me': AboutMe
    },
    name: 'App'
  }

</script>

