<template>
  <div class="page about">
    <div class="half left">
      <div class="author-photo cover-center"></div>
    </div>

    <div class="half right">
      <div class="block-header">
        <div class="text">ОБО МНЕ</div>
        <div class="divider"></div>
      </div>

      <div class="main-info">
        <p>Меня зовут Наталья Летунова. Я работаю архитектором и увлекаюсь фотографией. Мой путь начался совсем недавно, но
          я уже успела поучавствовать во многих выставках и заслужить некоторые призы.
        </p>
        <p>А университете я училась на архитектора, тогда же мне в руки попал и мой первый фотоаппарат, я начала изучать настройки
          и для меня открылся новый мир фотографии в который я погрузилась с головой.
        </p>
        <p>
          Сейчас я много путешествую и снимаю, в основном архитектуру.
        </p>
      </div>

      <div class="links">
        <div>
          <v-icon small>far fa-envelope</v-icon>
          <a href="/s">naletu@gmail.com</a>
        </div>
        <div>
          <v-icon small>fab fa-vk</v-icon>
          <a href="/s">nalletu</a>
        </div>
        <div>
          <v-icon small>fab fa-instagram</v-icon>
          <a href="/s">@naletu</a>
        </div>
        <div>
          <v-icon small>fab fa-facebook-square</v-icon>
          <a href="/s">natalya.letunova</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    name: 'AboutMe'
  }

</script>

<style scoped>
  .block-header {
    margin-bottom: 25px;
  }

  .half {
    width: 50%;
  }

  .main-info {
    margin: 0 25px;
    text-align: justify;
  }

  .left .author-photo {
    width: 100%;
    height: 580px;
    background-image: url('./../assets/main-girl.png');
  }

  .links {
    text-align: left;
    margin: 35px 25px;
  }

  .links i {
    width: 10px;
    margin-right: 10px;
    color: rgb(42, 42, 42);
  }

  .links a {
    color: #fe5e41;
    text-decoration: none;
  }

</style>



