<template>
  <div class="landing">
    <div class="background-video box">
      <video preload="metadata" autoplay>
        <source src="./../assets/city_main.mp4" type="video/mp4">
        <source src="./../assets/city_main.mp4" type="video/webm">
      </video>
    </div>

    <div class="top-tags">
      <div class="left">
        NALETU
      </div>
      <div class="right">
        <v-layout class="menu" wrap>
          <img src="./../assets/menu.svg" @click.stop="drawer = !drawer">

          <v-navigation-drawer v-model="drawer" temporary absolute right>
            <v-list class="pa-1">
              <v-list-tile avatar>
                <v-list-tile-avatar>
                  <img src="https://randomuser.me/api/portraits/men/85.jpg">
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>John Leider</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>

            <v-list class="pt-0" dense>
              <v-divider></v-divider>
              <v-list-tile v-for="item in items" :key="item.title" @click="">
                <v-list-tile-action>
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-navigation-drawer>
        </v-layout>
      </div>
    </div>

    <div class="middle-tags">
      <div class="name">ФОТОГРАФ НАТАЛЬЯ ЛЕТУНОВА</div>
      <div class="description">
        Путешествую с любовью
        <br> к архитектуре
      </div>
    </div>

    <div class="bottom-tags">
      <div class="left links">
        <a href="/s">
          <v-icon>fab fa-500px</v-icon>
        </a>
        <a href="/s">
          <v-icon>fab fa-behance</v-icon>
        </a>
        <a href="/s">
          <v-icon>fab fa-facebook-square</v-icon>
        </a>
        <a href="/s">
          <v-icon>fab fa-vk</v-icon>
        </a>
        <a href="/s">
          <v-icon>fab fa-instagram</v-icon>
        </a>
      </div>
      <div class="right">
        <div v-on:click="changeLang('ru')">
          РУС
          <div v-if="lang === 'ru'" class="underline-divider"></div>
        </div>
        <div v-on:click="changeLang('eng')">
          ENG
          <div v-if="lang === 'eng'" class="underline-divider"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        drawer: null,
        lang: "ru",
        items: [{
            title: 'Home',
            icon: 'dashboard'
          },
          {
            title: 'About',
            icon: 'question_answer'
          }
        ]
      }
    },
    methods: {
      changeLang(newLanguage) {
        this.lang = newLanguage;
      },
    },
    name: 'landing'
  }

</script>

<style scoped>
  .landing {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    margin-bottom: 100px
  }

  .background-video.box {
    text-align: center;
    margin: 0 -1000px;
    height: 100%;
  }

  .background-video.box video {
    display: inline-block;
    vertical-align: top;
    height: 100vh;
    min-height: 768px;
    transform: scale(1.15);
  }

  .left {
    position: absolute;
    left: 50px;
  }

  .right {
    position: absolute;
    right: 50px;
  }

  .top-tags {
    position: absolute;
    top: 50px;
    color: white;
    font-size: 18px;
  }

  .top-tags .right {
    top: -50px;
    right: 0;
  }

  .right .menu {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 2;
  }

  .right .menu img {
    position: absolute;
    top: 50px;
    right: 50px;
    width: 35px;
    cursor: pointer;
  }

  .middle-tags {
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: rgb(255, 255, 255);
  }

  .middle-tags .name {
    font-size: 15px;
    letter-spacing: 2px;
    line-height: 3.467;
  }

  .middle-tags .description {
    font-size: 56px;
    letter-spacing: 2px;
    font-weight: bold;
    line-height: 1.179;
  }

  .bottom-tags {
    position: absolute;
    bottom: 50px;
    width: 100vw;
    z-index: 3;
  }

  .bottom-tags .right {
    cursor: context-menu;
  }

  .bottom-tags .links a {
    text-decoration: none;
  }

  .bottom-tags .links i {
    font-size: 22px;
    margin-right: 20px;
    color: white;
  }

  .bottom-tags .right div:first-child {
    margin-right: 10px;
  }

  .bottom-tags .right div {
    display: inline-grid;
    line-height: 1;
    color: white;
    vertical-align: bottom;
    cursor: pointer;
  }

  .underline-divider {
    height: 1px;
    width: 100%;
    margin-top: 3px;
    background-color: #fe5e41;
  }

</style>



