<template>
  <div class="page main">
    <div class="presets">
      <div class="block-header">
        <div class="ledtDivider divider"></div>
        <div class="text">АВТОРСКИЕ ПРЕСЕТЫ</div>
        <div class="rightDivider divider"></div>
      </div>

      <div class="preset-block">
        <div class="change-arrows">
          <img src="./../assets/next.svg" class="revert" />
        </div>
        <div style="width: 74%">
          <split-panel :gutter="4" :init="30" style="height: 575px">
            <div slot="1" class="split-image from" style="position: relative">
              <p class="beforeLabel">
                ДО
              </p>
            </div>
            <div slot="2" class="split-image to" style="position: relative">
              <p class="afterLabel">
                ПОСЛЕ
              </p>
            </div>
          </split-panel>
        </div>
        <div class="change-arrows">
          <img src="./../assets/next.svg" />
        </div>
      </div>

      <div class="preset-info">
        <div class="text">
          Пресет:
          <span style="color: #fe5e41">«О дивный новый мир»</span>
        </div>

        <v-btn class="more">
          <div class="moreText">
            БОЛЬШЕ ПРЕСЕТОВ
          </div>
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
  import landing from './Landing.vue'
  import splitPanel from './SplitPanel.vue'

  export default {
    data() {
      return {}
    },
    created() {},
    components: {
      'landing': landing,
      splitPanel
    },
    name: 'App'
  }

</script>

<!-- Add "scoped " attribute to limit CSS to this component only -->
<style scoped>
  .presets .text {
    margin: 0 21px;
    font-size: 15px;
    font-weight: bold;
  }

  .presets {
    max-width: 1180px;
    padding-top: 70px;
    padding-bottom: 0.5px;
    margin: 100px auto;
    width: 100%;
  }

  .split-box {
    width: 100%;
    height: 575px;
    position: relative
  }

  .split-image {
    width: 100%;
    height: 100%;
    background-size: 873px;
  }

  .split-image1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 575px;
    background-image: url('./../assets/from.png');
    background-size: 873px;
    position: absolute;
  }

  .split-image.from {
    background-image: url('./../assets/from.png');
    background-position: left;
    width: 873px;
    background-size: cover;
  }

  .split-image.to {
    background-image: url('./../assets/to.png');
    /*width: 1180px;*/
    background-position: right;
  }

  .preset-block {
    display: flex
  }

  .change-arrows {
    height: 575px;
    width: 13%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.5;
  }

  .change-arrows img {
    width: 30px;
    cursor: pointer;
  }

  .revert {
    transform: rotate(180deg)
  }

  .preset-info {
    margin-top: 30px;
    margin-bottom: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .preset-info .text {
    font-size: 12px;
  }

  .preset-info .more {
    margin-top: 30px;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
  }

  .moreText {
    margin-left: 25px;
    margin-right: 25px;
    text-align: center;
    letter-spacing: 1px;
  }

  .main {
    min-width: 100%;
    padding-left: 0px;
    padding-right: 0px;
    background-color: #f5f5f5;
    margin-bottom: 100px;
  }

  .beforeLabel {
    position: absolute;
    bottom: 7px;
    left: 30px;
    color: white;
    font-weight: bold;
  }

  .afterLabel {
    position: absolute;
    bottom: 7px;
    right: 30px;
    color: white;
    font-weight: bold;
  }

</style>



