<template>
  <div class="page photos">
    <div class="block-header">
      <div class="divider"></div>
      <div class="text">ФОТОГРАФИИ ПО КАТЕГОРИЯМ</div>
      <div class="divider"></div>
    </div>

    <div>
      <div class="photo full-photo cover-center">
        <span class="top">КОГДА ТВОЯ РАБОТА ХОББИ, ТЫ ПО-НАСТОЯЩЕМУ СЧАСТЛИВ</span>
        <span class="middle">СТРАНЫ, ГОРОДА И АРХИТЕКТУРА</span>
        <div class="divider"></div>
        <span class="bottom">УВИДЕТЬ СНИМКИ</span>
      </div>

      <div class="photo third-photo street cover-center">
        <span class="top">ПРИРОДА, КОТОРУЮ СТОИТ УВИДЕТЬ</span>
        <span class="middle">ПЕЙЗАЖИ</span>
        <div class="divider"></div>
        <span class="bottom">УВИДЕТЬ СНИМКИ</span>
      </div>

      <div class="photo third-photo girl cover-center">
        <span class="top">КАЖДЫЙ ЧЕЛОВЕК КРАСИВ ПО-СОВЕМУ</span>
        <span class="middle">ПОРТРЕТЫ</span>
        <div class="divider"></div>
        <span class="bottom">УВИДЕТЬ СНИМКИ</span>
      </div>

      <div class="photo third-photo keyboard cover-center">
        <span class="top">НЕ ВСЕ ПОДДАЕТСЯ КЛАССИФИКАЦИИ</span>
        <span class="middle">ДРУГИЕ ФОТО</span>
        <div class="divider"></div>
        <span class="bottom">УВИДЕТЬ СНИМКИ</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    name: 'Photos'
  }

</script>

<style scoped>
  .photos span {
    color: rgb(210, 210, 210);
    position: absolute;
    z-index: 2;
  }

  .photos span.top {
    top: 30px;
  }

  .photos span.middle {
    top: 50%;
    font-size: 22px;
    font-weight: bold;
  }

  .photos .photo .divider {
    z-index: 2;
    background-color: white;
    position: absolute;
    bottom: 60px;
    width: 50px;
  }

  .photos span.bottom {
    bottom: 30px;
    font-size: 12px;
  }

  .photo {
    display: flex;
    justify-content: center;
    margin-right: 20px;
  }

  .photo:first-child,
  .photo:last-child {
    margin-right: 0;
  }

  .photo:after {
    content: '';
    position: absolute;
    width: 100%;
    left: 0;
    background: rgba(56, 56, 56, 0.48);
  }

  .full-photo {
    width: 100%;
    height: 430px;
    position: relative;
    background-image: url('./../assets/cities.png');
    margin-bottom: 20px;
  }

  .full-photo:after {
    height: 430px;
  }

  .third-photo {
    width: Calc(100%/3 - 16px);
    height: 580px;
    position: relative;
    display: inline-flex;
  }

  .third-photo:after {
    height: 580px;
  }

  .street {
    background-image: url('./../assets/street.png');
  }

  .girl {
    background-image: url('./../assets/girl.png');
  }

  .keyboard {
    background-image: url('./../assets/keyboard.png');
  }

  .увидеть_снимки {
    font-size: 12px;
    font-family: "Montserrat";
    color: rgb(254, 254, 254);
    text-transform: uppercase;
    line-height: 1.667;
    text-align: center;
    position: absolute;
    left: 810.047px;
    top: 2126.992px;
    width: 122px;
    height: 11px;
    z-index: 46;
  }

</style>



