<template>
  <v-app>
    <router-view></router-view>
    <site-footer></site-footer>
  </v-app>
</template>

<script>
  import Footer from './components/Footer.vue'

  export default {
    data() {
      return {
        clipped: false,
        drawer: true,
        fixed: false,
        items: [{
          icon: 'bubble_chart',
          title: 'Inspire'
        }],
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'Vuetify.js'
      }
    },
    components: {
      'site-footer': Footer
    },
    name: 'App'
  }

</script>

<style>
  #app {
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    background-color: white;
  }

  body {
    margin: 0;
  }

  .page {
    width: 100%;
    max-width: 1280px;
    padding: 0 50px;
    margin: auto;
  }

  .block-header {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-bottom: 70px;
  }

  .block-header .divider {
    height: 1px;
    background-color: #fe5e41 !important;
  }

  .block-header .text {
    margin: 0 21px;
    font-size: 15px;
    font-weight: bolder;
    letter-spacing: 2px;
  }

  .cover-center {
    background-size: cover;
    background-position: center;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }

  .preset-info button.more .btn__content {
    color: white;
    font-size: 10px;
    font-weight: 600;
    background-color: rgb(252, 94, 66);
    font-family: 'Montserrat', sans-serif;
  }

</style>



